import React from "react";
import ReactDOM from "react-dom/client";
import reportWebVitals from "./reportWebVitals";
import { BrowserRouter, Routes, Route } from "react-router-dom";
import { GoogleOAuthProvider } from "@react-oauth/google";
import "./index.css";
import App from "./App";
import Signin from "./Signin";
import Header from "./components/Header";
import { useLocation } from "react-router-dom";
import { AuthContextProvider } from "./store/AuthContextProvider";
import { InfoContextProvider } from "./store/InfoContextProvider";
import "./index.css";
import ExampleBot from "./ExampleBot";
import BuyCredits from "./BuyCredits";
import PrivateRoute from "./router/PrivateRoute";
import Verify from "./Verify";

const root = ReactDOM.createRoot(
  document.getElementById("root") as HTMLElement
);
const AppWithHeader = () => {
  const location = useLocation();
  const isAuthPage =
    location.pathname === "/sign-in" || location.pathname === "/register" || location.pathname.includes('/verify');

  return (
    <div className=" min-h-screen pb-10">
      {!isAuthPage && <Header />}

      <Routes>
        <Route path="/" element={
          <App />
        } />
        <Route path="/sign-in" element={<Signin />} />
        <Route path="/verify/:token" element={<Verify />} />
        <Route path="/example-bot/:botName" element={
          <PrivateRoute>
            <ExampleBot />
          </PrivateRoute>
        } />
        <Route path="/buy-credits" element={
          <PrivateRoute>
            <BuyCredits />
          </PrivateRoute>} />
      </Routes>
      <div className="bg-[#323746] fixed bottom-0 text-center dark-text-color font-medium sm:text-xs text-[10px] py-1 px-1">
        Version 1.0 Beta. We're diligently refining the experience and
        appreciate your understanding as we work towards perfection.
      </div>
    </div>
  );
};

root.render(
  <React.StrictMode>
    <GoogleOAuthProvider clientId={process.env.REACT_APP_GOOGLE_CLIENT_ID || ''}>
      <AuthContextProvider>
        <InfoContextProvider>
          <BrowserRouter>
            <AppWithHeader />
          </BrowserRouter>
        </InfoContextProvider>
      </AuthContextProvider>
    </GoogleOAuthProvider>
  </React.StrictMode>
);

// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();
