import React, { useReducer, useContext, createContext, useMemo } from "react";

type State = {
    [x: string]: string | number;
};

type Actions = {
    setBalances: (value: number) => void;
};

const INIT_STATE = {
    balances: -1,
};

const InfoContext = createContext<[State, Actions]>([
    INIT_STATE,
    {
        setBalances: (_value: number) => { },
    },
]);

InfoContext.displayName = "InfoContext";

function reducer(state: object, { type, payload }: { type: string; payload: string | number }) {
  return {
    ...state,
    [type]: payload,
  };
}

type ProviderProps = {
    children: React.ReactNode;
  };
  
  const InfoContextProvider = (props: ProviderProps) => {
    const [state, dispatch] = useReducer(reducer, INIT_STATE);
  
    const setBalances = (value: number): void => dispatch({ type: "balances", payload: value });
  
    return (
      <InfoContext.Provider
        value={useMemo(
          () => [
            state,
            {
              setBalances,
            },
          ],
          [state],
        )}
      >
        {props.children}
      </InfoContext.Provider>
    );
  };
  
  const useInfoContext = () => {
    const context = useContext(InfoContext);
    if (context === undefined) {
      throw new Error("useAuthContext must be used within a AuthProvider");
    }
    return context;
  };
  
  export { InfoContextProvider, useInfoContext };