const ChatMessage: React.FC<ChatMessageProps> = ({ message }) => {
  const { botName } = useParams();
  return (
    <div className={`chat-message ${message.sender}`}>
      {message.sender === "bot" && (
        <div className="avatar">
          <img src={"/images/wsm/" + botName+ "/" + botName+"-1.png"} alt="avatar" />
          <span className="botname">{botName}</span>
        </div>
      )}
      <div className="message-content">{message.content}</div>
    </div>
  );
};

interface ChatInputProps {
  onSubmit: (message: string) => void;
}

const ChatInput: React.FC<ChatInputProps> = ({ onSubmit }) => {

  const [{balances}, {setBalances}] = useInfoContext();

  const [message, setMessage] = useState("");

  const handleSubmit = (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    if (message.trim()) {
      onSubmit(message.trim());
      setMessage("");
    }
  };

  return (
    <div className="chat-input-wrapper">
      <form className="chat-input" onSubmit={handleSubmit}>
        <input
          type="text"
          value={message}
          onChange={(e) => setMessage(e.target.value)}
          placeholder="Send a message..."
          disabled={balances == 0}
        />
        <button type="submit" disabled={balances == 0}>
          <svg
            xmlns="http://www.w3.org/2000/svg"
            fill="none"
            viewBox="0 0 24 24"
            strokeWidth={1.5}
            stroke="currentColor"
            className="w-6 h-6"            
          >
            <path
              strokeLinecap="round"
              strokeLinejoin="round"
              d="M6 12L3.269 3.126A59.768 59.768 0 0121.485 12 59.77 59.77 0 013.27 20.876L5.999 12zm0 0h7.5"
            />
          </svg>
        </button>
      </form>
    </div>
  );
};

export default function ExampleBot() {
  const navigate = useNavigate();

  const [{ balances }, { setBalances }] = useInfoContext()

  const [messages, setMessages] = useState<Message[]>([]);
  const [loading, setLoading] = useState<boolean>(false);
  const [hasMounted, setHasMounted] = useState<boolean>(false);
  const messagesEndRef = useRef<HTMLDivElement>(null);
  // about modal
  const [modalOpen, setModalOpen] = useState(false);

  const { botName } = useParams();

  console.log('botname = ', botName)

  const addMessage = async (content: string) => {

    setMessages((prevMessages) => [
      ...prevMessages,
      { id: uuidv4(), content, sender: "user" },
    ]);

    let responseMessage = "";
    // Introduce delay before showing loading icon
    setTimeout(() => {
      setLoading(true); // Show loading icon

      // Introduce delay before adding bot's message and hiding loading icon
      setTimeout(async () => {
        const path = process.env.REACT_APP_BASE_URL + "/api/chat";
        const user_email = localStorage.getItem('email')
        const request = {
          email: user_email,
          query: content,
          botName: botName
        }

        await axios.post(path, request, {
          headers: {
            'Authorization': localStorage.getItem('token')
          },
        }).then((res) => {
          console.log(res.data);
          responseMessage = res.data.message
          getbalances()
        })

        setMessages((prevMessages) => [
          ...prevMessages,
          {
            id: uuidv4(),
            content:
              responseMessage,
            sender: "bot",
          },
        ]);
        setLoading(false); // Hide loading icon once bot's response is added
      }, 3500);
    }, 1000);
  };

  const getbalances = async () => {
    const path = process.env.REACT_APP_BASE_URL + "/api/getConnectInfo";
    const email = localStorage.getItem('email')

    const req = {
      email: email
    }

    await axios.post(path, req, {
      headers: {
        'Authorization': localStorage.getItem('token')
      }
    }).then((res) => {
      console.log(res.data.info.connects)
      setBalances(res.data.info.connects)
    }).catch(err => {
      console.log(err);
    })
  }

  useEffect(() => {
    if(balances == 0)
      alert('Please buy connects')
  },[balances])

  useEffect(() => {
    setHasMounted(true);
    getChatInfos()
  }, []);

  useEffect(() => {
    if (hasMounted) {
      messagesEndRef.current?.scrollIntoView({ behavior: "smooth" });
    }
  }, [loading]);

  useEffect(() => {
    if (hasMounted) {
      messagesEndRef.current?.scrollIntoView({ behavior: "smooth" });
    }
  }, [messages]);

  const getChatInfos = async () => {
    const path = process.env.REACT_APP_BASE_URL + "/api/getChatInfos";
    const user_email = localStorage.getItem('email')
    const request = {
      email: user_email,
      botName: botName
    }
    await axios.post(path, request, {
      headers: {
        'Authorization': localStorage.getItem('token')
      }
    }).then((res) => {
      let data = []
      console.log('chatData = ', res.data.chat)
      if (res.data.chat.chats && res.data.chat.chats.length) {
          for (var i = 0; i < res.data.chat.chats.length; i++) {
            data.push({
              id: uuidv4(),
              content: res.data.chat.chats[i].question,
              sender: 'user'
            })
            data.push({
              id: uuidv4(),
              content: res.data.chat.chats[i].answer,
              sender: 'bot'
            })
          }
        setMessages(data)
      }
    }).catch((err) => {
      console.log(err)
    })
  }

  return (
    <div className="flex sm:grid sm:grid-cols-2 flex-col md:flex-row justify-center items-start my-4 sm:my-6 mx-4 sm:mx-auto px-0 sm:px-6 max-w-7xl">
      {/* Profile */}
      <div className="profile-container px-0 sm:px-3">
        <div className="profile-header">
          <div className="profile-header-inner">
            <div className="profile-avatar">
              <img
                className="rounded-3xl"
                src={"/images/wsm/" + botName+ "/" + botName+"-1.png"}
                alt="avatar"
              />
            </div>
            <div className="flex items-center dark-text-color mt-5 justify-between mb-5">
              <h1 className="text-2xl font-bold">
                {botName}
                <span className="ml-2 opacity-70">24</span>
              </h1>
              <button
                onClick={() => setModalOpen(true)}
                className="flex text-sm font-bold items-center text-primary hover:text-white transition "
              >
                Read my bio
                <svg
                  xmlns="http://www.w3.org/2000/svg"
                  fill="none"
                  viewBox="0 0 24 24"
                  strokeWidth={1.5}
                  stroke="currentColor"
                  className="w-3 h-3 ml-1"
                >
                  <path
                    strokeLinecap="round"
                    strokeLinejoin="round"
                    d="M4.5 12h15m0 0l-6.75-6.75M19.5 12l-6.75 6.75"
                  />
                </svg>
              </button>
            </div>
            <h2 className="dark-text-color opacity-70 leading-78 mb-4 italic">
              "Lorem ipsum dolor sit amet, consectetur adipi scing elit. Nulla
              vitae. Consectetur adipi scing"
            </h2>
          </div>
        </div>
      </div>

      <AboutModal open={modalOpen} setOpen={setModalOpen} />

      {/* Chat */}
      <div className="pr-0 w-full sm:px-3 px-0">
        <div className="chat-container">
          <div className="chat-header-and-messages">
            <div className="chat-header">
              <div className="chat-header-inner">
                <div className="ai-avatar">
                  <img src={"/images/wsm/" + botName+ "/" + botName+"-1.png"} alt="avatar" />
                </div>
                <div className="ai-name">
                  <h3>{botName}</h3>
                  <div className="ai-status">
                    online
                    <span className="ai-status-circle"></span>
                  </div>
                </div>
              </div>
            </div>
            <div className="chat-inner">
              <div className="chat-messages">
                {messages.map((message) => (
                  <ChatMessage key={message.id} message={message} />
                ))}
                {loading && (
                  <div className="chat-message bot">
                    <div className="avatar">
                      <img src={"/images/wsm/" + botName+ "/" + botName+"-1.png"} alt="avatar" />
                      <span className="botname">{botName}</span>
                    </div>
                    <div className="message-conten">
                      <LoadingIcon />
                    </div>
                  </div>
                )}
                <div ref={messagesEndRef} />
              </div>
            </div>
          </div>
          <ChatInput onSubmit={addMessage} />
        </div>
      </div>
    </div>
  );
}
