import React, { useReducer, useContext, createContext, useMemo } from "react";

type State = {
    [x: string]: string | boolean;
};

type Actions = {
    setIsAuthenticated: (value: boolean) => void;
};

const INIT_STATE = {
    isAuthenticated: false,
};

const AuthContext = createContext<[State, Actions]>([
    INIT_STATE,
    {
        setIsAuthenticated: (_value: boolean) => { },
    },
]);

AuthContext.displayName = "AuthContext";

function reducer(state: object, { type, payload }: { type: string; payload: string | boolean }) {
  return {
    ...state,
    [type]: payload,
  };
}

type ProviderProps = {
    children: React.ReactNode;
  };
  
  const AuthContextProvider = (props: ProviderProps) => {
    const [state, dispatch] = useReducer(reducer, INIT_STATE);
  
    const setIsAuthenticated = (value: boolean): void => dispatch({ type: "isAuthenticated", payload: value });
  
    return (
      <AuthContext.Provider
        value={useMemo(
          () => [
            state,
            {
              setIsAuthenticated,
            },
          ],
          [state],
        )}
      >
        {props.children}
      </AuthContext.Provider>
    );
  };
  
  const useAuthContext = () => {
    const context = useContext(AuthContext);
    if (context === undefined) {
      throw new Error("useAuthContext must be used within a AuthProvider");
    }
    return context;
  };
  
  export { AuthContextProvider, useAuthContext };